.contactPage {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
}

.contactPageImage {
  width: 100%;
  height: 420px;
  margin-bottom: 50px;
}

.contactPageImage > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contactPageMain {
  padding: 0 16px;
  display: flex;
  gap: 64px;
}

.contactPageMainInfoTitle {
  margin-bottom: 34px;
  text-transform: uppercase;
  font-weight: 400;
  font-size: var(--fs-400);
  letter-spacing: 1px;
}

.contactPageMainInfoText {
  font-size: var(--fs-300);
  line-height: 32px;
}

.contactPageAside {
  min-width: 240px;
  max-width: 340px;
  margin: 0 auto;
}

.contactPageAsideLocation {
  text-align: center;
  padding: 40px 0;
  margin-bottom: 64px;
  border-bottom: 1px dashed lightgray;
}

.contactPageAsideLocationTitle {
  margin-bottom: 32px;
  position: relative;
  font-size: var(--fs-200) !important;
  letter-spacing: 1px;
}

.contactPageAsideLocationTitle:after {
  content: "";
  position: absolute;
  bottom: -75%;
  left: 50%;
  transform: translateX(-50%);
  height: 1px;
  width: 16px;
  background-color: black;
}

.contactPageAsideLocationText {
  color: gray;
  font-style: italic;
  font-weight: 400;
  font-size: var(--fs-200);
}

.contactPageAsideLocationText > span {
  color: black;
}

.contactPageAsideLocationImage {
  width: 100%;
}

.contactPageAsideLocationImage > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contactPageAsideLocationTitle {
  font-size: 16px;
  text-transform: uppercase;
}

.contactPageAsideBanner {
  width: 100%;
}

.contactPageAsideBanner > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 1024px) {
  .contactPageMain {
    flex-direction: column;
  }
}
